<template>
  <div class="recharge-channel">
    <nav-head title="银行账户" FixedOrTransparency="isFixed NoTransparency"/>
    <div class="item-wrap">
      <div class="item">
        <span>真实姓名</span>
        <input type="text" v-model="user_name" placeholder="请输入开户姓名"/>
      </div>
      <div class="item" @click="onShow">
        <span>银行名称</span>
        <input type="text" v-model="bank_name" class="bank" disabled placeholder="请选择银行"/>
        <span class="arrow-right"></span>
      </div>
      <div class="item">
        <span>卡号</span>
        <input type="text" maxlength="25" v-model="bank_num" @input="handleInputNum" placeholder="请输入银行卡卡号"/>
      </div>
      <div class="item">
        <span>开户网点</span>
        <input type="text" v-model="open_bank" placeholder="请输入开户网点"/>
      </div>
    </div>
    <div class="item-wrap">
      <div class="item" style="margin-top: 18px;">
        <span>提现密码</span>
        <input type="password" maxlength="6" v-model="bank_password" placeholder="6位数字，用于提现时的身份验证"/>
      </div>
    </div>
    <div class="padding-0-15"><mt-button size="large" v-bind:disabled="!bank_num || !bank_name || !open_bank || !bank_password" @click.native="bindBank" class="btn-bind" v-bind:class="{'available': bank_num && bank_name && open_bank && bank_password }">绑定银行卡</mt-button></div>
    <bank-picker v-bind:options="bankList" v-show="this.show" @close="onClose" @setValue="setRadioValue"></bank-picker>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import { Button, Toast } from 'mint-ui'
import { mapActions } from 'vuex'
import http from '../../../tool/http'
import { ADD_USER_BANK } from '../../../vuex/Mine'
import BankPicker from './BankPicker.vue'

export default {
  name: 'bindBank',
  data () {
    return {
      bank_id: '',
      bank_name: '',
      bank_num: '',
      open_bank: '',
      bank_password: '',
      show: false,
      bankList: '',
      user_name: ''
    }
  },
  methods: {
    setRadioValue (obj) {
      this.bank_id = obj.id
      this.bank_name = obj.name
    },
    bindBank () {
      if (!this.bank_id && !this.bank_name) {
        Toast('请选择银行!')
        return
      }
      if (!(new RegExp(`^([1-9]{1})(\\d{14}|\\d{15,25})$`).test(this.bank_num)) || !this.bank_num) {
        Toast('请输入正确的银行卡卡号!')
        return
      }
      if (!this.open_bank) {
        Toast('请输入开户网点!')
        return
      }
      if (!((new RegExp(`^(([a-zA-Z+\\.?\\·?a-zA-Z+]{2,30}$)|([\u4e00-\u9fa5+\\·?\u4e00-\u9fa5+]{2,30}$))`)).test(this.user_name)) || !this.user_name) {
        Toast('请输入开户姓名!')
        return
      }
      if (!(/^\d+$/.test(this.bank_password)) || !this.bank_password) {
        Toast('请输入银行卡提现密码!')
        return
      }
      this.addUserBank({
        bank_id: this.bank_id,
        bank_name: this.bank_name,
        bank_num: this.bank_num,
        bank_password: this.bank_password,
        open_bank: this.open_bank,
        user_name: this.user_name
      }).then(data => {
        Toast('银行卡绑定成功')
      }).finally(() => {
        this.$router.back()
      })
    },
    handleInputNum (e) {
      this.bank_num = e.target.value.replace(/[^\d]/g, '')
    },
    onClose () {
      this.show = false
    },
    onShow () {
      http.getBankList().then(data => {
        if (data) {
          this.bankList = data
          this.show = true
        } else {
          Toast('获取银行列表失败')
        }
      })
    },
    ...mapActions({
      addUserBank: ADD_USER_BANK
    })
  },
  components: {
    NavHead,
    [Button.name]: Button,
    BankPicker
  }
}
</script>
<style scoped lang="scss">
  .item-wrap {
    background-color: $background-light-color;
    padding: 0 15px;
  }
  .item {
    height: 54px;
    line-height: 54px;
    text-align: left;
    font-size: 17px;
    position: relative;
    overflow: hidden;
    border-bottom: 1px solid $border-color;
    span:first-child {
      color: $font-color;
    }
    input {
      float: right;
      border: none;
      outline: none;
      display: inline-block;
      width: 75%;
      position: absolute;
      right: 0;
      top: 0;
      padding: 0 15px;
      font-size: 15px;
      height: 54px;
      line-height: 54px;
      color: #CCC;
      background: transparent;
      border-radius: 0;
      text-align: right;
    }
    input.bank {
      margin-right: 15px;
    }
    .arrow-right {
      position: absolute;
      right: 0;
      top: 15px;
    }
  }
  .item:last-child {
    border-bottom: none;
  }

  button.btn-bind {
    border: none;
    background-color: #ccc;
    color: white;
    box-shadow: none;
    height: 48px;
    font-size: 17px;
    margin-top: 40px;
  }
  button.btn-bind.available{
    background-color: $back-color;
    @if $IF365 { color: #131313 }
  }
  .padding-0-15 {
    padding: 0 15px;
  }
</style>
