<template>
  <div class="bank-picker">
    <div class="mask" @click="onClose"></div>
    <div class="content">
      <div class="title">选择银行<mt-button @click.native="onClose" class="btn-close"><img src="../../../assets/mine/ic_close_365@2x.png" height="22" width="22" slot="icon"></mt-button></div>
      <div class="radio-wrap">
        <div v-for="item in options" v-bind:key="item.id" class="ui-radio">
          <input v-bind:id="item.id" type="radio" name="radio" v-model="value" v-bind:value="item.name" @change="handle($event)">
          <label v-bind:for="item.id" >{{item.name}}</label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Button } from 'mint-ui'

export default {
  name: 'bankPicker',
  props: {
    options: { type: Array }
  },
  data: function () {
    return {
      value: ''
    }
  },
  methods: {
    onClose () {
      this.$emit('close')
    },
    handle (event) {
      const id = event.currentTarget.id
      this.$emit('setValue', { id: id, name: this.value })
      this.$emit('close')
    }
  },
  components: {
    [Button.name]: Button
  }
}
</script>

<style scoped lang="scss">
  .bank-picker {
    position: absolute;
    z-index: 2035;
    width: 100%;
    height: 100%;
    max-width: 640px;
    top: 0;
    left: 0;
    div.mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0,0,0,0.5);
      z-index: 2;
    }
    div.content {
      position: absolute;
      bottom: 0;
      z-index: 3;
      width: 100%;
      padding: 0 25px;
      height: 45%;
      background-color: $background-light-color;
      overflow-y: scroll;
      text-align: left;
      .title {
        color: #CCC;
        font-size: 15px;
        height: 45px;
        line-height: 45px;
        position: fixed;
        left: 0;
        width: 100%;
        padding: 0 25px;
        z-index: 6;
        background-color: $background-light-color;
        .btn-close {
          background: none;
          border: none;
          box-shadow: none;
          float: right;
          height: 30px;
          width: 30px;
          text-align: right;
          padding: 0;
          margin-top: 10px;
        }
      }
      .radio-wrap {
        margin-top: 40px;
        position: relative;
        z-index: 5;
        .ui-radio {
          display: block;
          border-bottom: 1px solid $border-color;
          position: relative;
          height: 49px;
          line-height: 49px;
          font-size: 15px;
          color: $font-color;
          label {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
          }
          input[type="radio"] {
            opacity: 0;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
          }
          input[type="radio"]:checked + label{
            @if $IF365 { background-image: url("../../../assets/mine/ic_address_pic_365@2x.png"); }
            @else { background-image: url("../../../assets/mine/ic_address_pic@2x.png"); }
            background-repeat: no-repeat;
            background-position: right center;
            background-size: auto 50%;
            color: $back-color;
          }
        }
        .ui-radio:last-child {
          border-bottom: none;
        }
      }
    }
  }
</style>
